

.todo-container{
    width: 15rem;
    height: 12rem;
    //border-radius: 0.8rem 0 0 0.8rem;
    position: relative;
    margin: 2% 0%;
    display: inline-block;
    border-right: 10px solid #312F3C;
    font-family: 'Roboto', sans-serif;
    color: #fff;
    font-weight: 500;
    
  
   
    


    button{
        width: 2rem;
        height: 2rem;
        border: none;
        outline: none;
        cursor: pointer;


    }

    .todo-complete{
        width: 1.5rem;
        height: 1.5rem;
        margin: 2% 0 0 2%;
        position: absolute;
        top: 2%;
        left: 2%;
        background-color: #FFFEFD;

        .complete-check {
            color: white;
            display: none;
        }

        .completed-check {
            display: block;
        }
        

     

    }

    .btn-completed {
        background-color: inherit;
        opacity: 1;
        z-index: 10;
    }

    .todo-item{
        width: 98%;
        height: 60%;
        list-style: none;
        position: absolute;
        font-weight: bolder;
        top: 25%;
        left: 5%;
        margin: 5% 0 0 0;
      
    
    }

    .todo-delete{
        height: 2.5rem;
        width: 2.5rem;
        background-color: inherit;
        outline: none;
        position: absolute;
        top: 2%;
        right: 2%;
        //border: 2px solid red;

       &:hover{
        opacity: 0.6;
       }

       .trash-icon{
        width: 1.1rem;
        height: 1.1rem;
        color: white;
       }
    }
}

.todo-container:nth-of-type(4n-3) {
   
    background-image: linear-gradient(62deg, #FBAB7E 0%, #F7CE68 100%);
    
}

.todo-container:nth-of-type(4n-2) {
    background-image: linear-gradient(-20deg, #00cdac 0%, #8ddad5 100%);
}
.todo-container:nth-of-type(4n-1) {
    background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
}
.todo-container:nth-of-type(4n) {
    background-image: linear-gradient(to right, #f78ca0 0%, #f9748f 19%, #fd868c 60%, #fe9a8b 100%);
}






.completed{
    opacity: 0.5;
    text-decoration: line-through;  
 
}