

.todo-form-container{
width: 90vw;
height: 15vh;
margin: 0 auto;
opacity: 0.8;
position: absolute;
top: 10%;
left: 5%;


    .form{
        width: 50%;
        height: 18%;
        position: absolute;
        top: 5%;
        left: 25%;
        display: flex;
        flex-direction: row;
        align-items: space-between;
        z-index: 100;
        
        ::placeholder{
            font-family: 'Roboto', sans-serif;
            font-weight: 500;
            color: #FFFEFD;
           
            
        }
       

        .todo-input{
            width: 120%;
            height: 150%;
            border: none;
            outline: none;
            border-radius: 1.5rem;
            background: rgba(73, 76, 93, 0.54);
            border-radius: 16px;
            box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
            backdrop-filter: blur(2.6px);
            -webkit-backdrop-filter: blur(2.6px);
            border: 1px solid rgba(73, 76, 93, 0.46);
            cursor: text;
            padding: 2%;
            position: absolute;
            font-family: 'Roboto', sans-serif;
            color: #D7D5DC;
            font-weight: 500;
      

           
            @media (min-width: 500px) {
                
            }

            @media (min-width: 900px) {
                width: 100%;
                height: 100%;

            }

        }

        .todo-submit{
            width: 1.8rem;
            height: 1.8rem;
            position: relative;
            top: 32%;
            left: 105%;
            cursor: pointer;
            outline: none;
            border-radius: 50%;
            border: none;
            background-color: #9FD6F5;
            z-index: 60;
            &:hover {
                opacity: 0.6;
            }

            .plus{
                  color: white;
                  position: absolute;
                  top: 25%;
                  left: 30%;
                  font-size: 0.8rem;

            @media (min-width: 500px) {
                font-size: 1.2rem;
                  }
      
            @media (min-width: 900px) {
                    top: 20%;
                    left: 25%;
                    font-size: 1.5rem;
      
                  }
            }

            @media (min-width: 500px) {
                width: 2.5rem;
                height: 2.5rem;
                top: 30%;
                left: 110%;
            }

            @media (min-width: 900px) {
                width: 2.5rem;
                height: 2.5rem;
                top: 25%;
                left: 95%;
                
            }
        }
       

       
    }

    
}